* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.appWrapper {
    display: grid;
    width: 1200px;
    margin: 0 auto;
    grid-template-areas:
            "header header header"
            "nav content content";

    grid-template-rows: 60px 1fr;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.appWrapperContent {
    grid-area: content;
    background-color: lightgreen;
}